import React, { useEffect, useState } from 'react';
import './about.css';
import Video from "../assets/3d_video.mp4"
import Category from "./category";
import { Link } from "gatsby";


const Project_image_garelly = () => {

  const img_clicked = (index) => {
    // console.log(index);
  }
  const garelly_sources = [
    "img/project1.jpg",
    "img/project2.jpg",
    "img/project3.jpg",
    "img/project1.jpg",
    "img/project2.jpg",
    "img/project3.jpg",
    "img/project1.jpg",
    "img/project2.jpg",
    "img/project3.jpg",
    "img/project1.jpg",
    "img/project2.jpg",
    "img/project3.jpg",
  ];

  return(
    <>
      <div className="modal_container">
        <h2>Project Title</h2>
        <button >&times;</button>
      </div>
      <div className="modal-content">

      </div>

      <div className="garelly_container">
        <div className="inner_container">
          <h1>Our Projects</h1>
            <div className="border"></div>
            <div className="garelly">
              {garelly_sources.map((e, i) => 
              <span className="img_box">
                <img 
                src={e} 
                alt="Project images"
                onClick={() => img_clicked(i)}
                />
              </span>
              )}
            </div>
        </div>
      </div>
    </>
  );
};


const classNames = [
  "civil",
  "mining",
  "automotive"
]
const linkSrc = [
  "/civil_architecture",
  "/mining_heavy-industry",
  "/automotive"
]
const About = () => {
  const [moreClicked, setClicked] = useState("");
  const [imgPath, setImgPath] = useState();
  const [linkPath, setlinkPath] = useState("");

  const handleCategory = () => {
    if (moreClicked !== 'active') {
      setClicked("active");
    } else {
      setClicked("");
    }
  }
  let index = 0;
  let timeHandle;
  useEffect(() => {
    timeHandle = setInterval(() => {
      switch (index) {
        case 0:
          setImgPath(classNames[0])
          setlinkPath(linkSrc[0])
          index++;
          break;
        case 1:
          setImgPath(classNames[1])
          setlinkPath(linkSrc[1])
          index++;
          break;
        case 2:
          setImgPath(classNames[2])
          setlinkPath(linkSrc[2])
          index++;
          break;
        default:
          index = 0;
          return;
      }
    }, 3000)
    return () => clearInterval(timeHandle);
  }, []);


  return (
    <div id="about">
      <Project_image_garelly />
      <div className="aboutContainer">
        <div className="flex1 flexHeight">
          <div className="fontFour textContainer">
            <h2>Our Projects</h2>
            <p>ManSysCo has been engaged, and repeatedly, successfully delivered a number of projects to our expanding base of clients.</p>
            <button onClick={handleCategory}>
              Learn more  <i 
              className="fas fa-angle-double-right"></i>
            </button>
          </div>
        </div>
        <div className= "flex2 flexHeight">
          <Link to={linkPath} >
            <div 
              id="project_images"
              className={imgPath}
            >
            </div>
          </Link>
          <Category
            divClass={"categories flexItem " + moreClicked}
          />
        </div>
      </div>
      
      <div className="aboutContainer">
        <div className="flex3 flexHeight">
            <video autoPlay loop preload="auto">
              <source src={Video} type="video/mp4"></source>
              <track default/>
            </video>
        </div>
        <div className="flex4 flexHeight">
          <div className="fontFour textContainer">
            <h2>Our Services</h2>
            <p>Our mission is your wish</p>
            <Link to="/service">
              <i className="fas fa-angle-double-left">
              </i>  Learn more 
            </Link>
          </div>
        </div>
      </div>
    </div>
  )
}

export default About;